import { createNativeStackNavigator } from "@react-navigation/native-stack";
import PaymentScheduleScreen from "../screen/Payment/PaymentScheduleScreen";
import RegisServiceContract from "../screen/Payment/RegisServiceContract";

const Payment = createNativeStackNavigator();
const PaymentScheduleNavigation = (propsx: any) => {
  return (
    <Payment.Navigator
      screenOptions={{
        headerShown: false,
      }}
    >
      <Payment.Screen name="PaymentScheduleScreen" component={PaymentScheduleScreen} />
      <Payment.Screen name="RegisServiceContract" component={RegisServiceContract} />
    </Payment.Navigator>
  )
}

export default PaymentScheduleNavigation;