import {FlatList, SafeAreaView, StyleSheet, View} from 'react-native';
import {Timeline} from 'react-native-ui-lib';
import {colors} from '../../../assets/color/color';
import {image_tick} from '../../../assets/images/images';
import InstallmentScheduleItem from './ActionSheet';

const InstallmentSchedule = (listInstallment: any) => {
  const firstItem = listInstallment.find(
    (item: any) => item.status === 'Chưa thanh toán',
  );

  const RenderTimelineItem = (installmentObj: any) => {
    return (
      <View style={{marginHorizontal: 20}}>
        <Timeline
          bottomLine={
            installmentObj.props !== listInstallment[listInstallment.length - 1]
              ? {
                  type: Timeline.lineTypes.SOLID,
                  state:
                  installmentObj.props.status !== firstItem.status
                      ? Timeline.states.CURRENT
                      : Timeline.states.NEXT,
                }
              : undefined
          }
          point={
            installmentObj.props.status !== firstItem.status
              ? {
                  icon: image_tick,
                  color: colors.primary,
                }
              : {
                  icon: 6,
                  color: 'gray',
                }
          }
          topLine={
            installmentObj.props !== listInstallment[0]
              ? {
                  type: Timeline.lineTypes.SOLID,
                  state:
                  installmentObj.props.status !== firstItem.status ||
                    (installmentObj.props === firstItem &&
                      installmentObj.props.status === firstItem.status &&
                      installmentObj.props !== listInstallment[0])
                      ? Timeline.states.CURRENT
                      : Timeline.states.NEXT,
                }
              : undefined
          }>
          <InstallmentScheduleItem itemInstallmentSchedule={installmentObj} />
        </Timeline>
      </View>
    );
  };

  return (
    <SafeAreaView style={styles.container}>
      <FlatList
        data={listInstallment}
        renderItem={({item}) => <RenderTimelineItem props={item} />}
      />
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    paddingBottom: 180,
  },
});

export default InstallmentSchedule;
